.footer-container{
    height: 17.8%;
    width: 100%;
    overflow: hidden;
    display: flex;
     justify-content: center;
}
.footer-parent{
    width: 100%;
     height: 100%;
    max-width: 1920px;
}
.footer-parent img{
    max-width: 100%;
    height: 130px;
}
@media only screen and (max-width: 1110px){
    .footer-parent {
        display: flex;
        align-items: center;
    }
}
@media only screen and (max-width: 875px){
    .footer-parent img{
        display: flex;
        align-items: center;
        height:100%
    }
}